import * as React from 'react'
import { geometry } from 'geospatialdraw'
import { useLazyQuery } from '@apollo/react-hooks'
import gql from 'graphql-tag'
import {
  Suggestion,
  KeywordProps as Props,
  ContainerProps,
  queryHook,
} from './props'

const SuggestionsQuery = gql`
  query SuggestionsQuery($q: String!) {
    suggestions(q: $q) {
      id
      name
    }
  }
`

const FeatureQuery = gql`
  query FeatureQuery($id: String!) {
    geofeature(id: $id) {
      type
      geometry
      properties
      id
    }
  }
`

type suggestionResult = {
  suggestions: Suggestion[]
}

type geofeatureResult = {
  geofeature: geometry.GeometryJSON
}

const withRemoteQueries = (
  Component: React.ComponentType<ContainerProps>
): React.SFC<Props> => ({ value, ...rest }) => {
  const [
    getFeature,
    {
      data: { geofeature } = { geofeature: value },
      loading: featureLoading,
      error: featureError,
    },
  ] = useLazyQuery<geofeatureResult>(FeatureQuery)
  const [
    getSuggestions,
    {
      data: { suggestions } = { suggestions: [] },
      loading: suggestionLoading,
      error: suggestionError,
    },
  ] = useLazyQuery<suggestionResult>(SuggestionsQuery)
  const useFeatureQuery: queryHook<geometry.GeometryJSON, string> = () => ({
    fetch: id =>
      getFeature({
        variables: {
          id,
        },
      }),
    data: geofeature,
    loading: featureLoading,
    error: featureError ? true : false,
  })
  const useSuggestionQuery: queryHook<Suggestion[], string> = () => ({
    fetch: q =>
      getSuggestions({
        variables: {
          q,
        },
      }),
    data: suggestions,
    loading: suggestionLoading,
    error: suggestionError ? true : false,
  })
  return (
    <Component
      useFeatureQuery={useFeatureQuery}
      useSuggestionQuery={useSuggestionQuery}
      value={value}
      {...rest}
    />
  )
}

export default withRemoteQueries
try {
    // @ts-ignore
    withremotequeries.displayName = "withremotequeries";
    // @ts-ignore
    withremotequeries.__docgenInfo = { "description": "", "displayName": "withremotequeries", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/location/keyword/with-remote-queries.tsx#withremotequeries"] = { docgenInfo: withremotequeries.__docgenInfo, name: "withremotequeries", path: "src/main/webapp/location/keyword/with-remote-queries.tsx#withremotequeries" };
}
catch (__react_docgen_typescript_loader_error) { }