import * as React from 'react'
import { coordinates as coordinateEditor } from 'geospatialdraw'
import EditorProps, { BasicEditorProps } from './geo-editor'
import Tab from '@material-ui/core/Tab'
import Tabs from '@material-ui/core/Tabs'
import SpacedLinearContainer from '../spaced-linear-container'

type OutputComponent = React.SFC<BasicEditorProps>
type EditorComponent = React.ComponentType<EditorProps>

const tabMap: coordinateEditor.CoordinateUnit[] = [
  coordinateEditor.LAT_LON,
  coordinateEditor.LAT_LON_DMS,
  coordinateEditor.USNG,
  coordinateEditor.UTM,
]

const defaultTab = 0

const withCoordinateUnitTabs = (Editor: EditorComponent): OutputComponent => ({
  value,
  onChange,
}) => {
  const [tab, setTab] = React.useState(defaultTab)
  return (
    <SpacedLinearContainer direction="column" spacing={1}>
      <Tabs
        value={tab}
        onChange={(_, index) => setTab(index)}
        indicatorColor="primary"
        textColor="primary"
        variant="fullWidth"
      >
        <Tab label="Lat / Lon (DD)" />
        <Tab label="Lat / Lon (DMS)" />
        <Tab label="USNG / MGRS" />
        <Tab label="UTM / UPS" />
      </Tabs>
      <Editor value={value} onChange={onChange} coordinateUnit={tabMap[tab]} />
    </SpacedLinearContainer>
  )
}

export default withCoordinateUnitTabs
try {
    // @ts-ignore
    withcoordinateunittabs.displayName = "withcoordinateunittabs";
    // @ts-ignore
    withcoordinateunittabs.__docgenInfo = { "description": "", "displayName": "withcoordinateunittabs", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/location/with-coordinate-unit-tabs.tsx#withcoordinateunittabs"] = { docgenInfo: withcoordinateunittabs.__docgenInfo, name: "withcoordinateunittabs", path: "src/main/webapp/location/with-coordinate-unit-tabs.tsx#withcoordinateunittabs" };
}
catch (__react_docgen_typescript_loader_error) { }