import * as React from 'react'
import TextField from '@material-ui/core/TextField'
import Props from './props'
import { coordinates as coordinateEditor } from 'geospatialdraw'

type ChangeEvent = React.ChangeEvent<HTMLInputElement>

const PointUSNG: React.SFC<Props> = ({ value, onChange }) => {
  const [
    coordinates,
    usng,
    setUSNG,
    isValid,
    formattedUSNG,
  ] = coordinateEditor.useUSNGCoordinates(value)
  React.useEffect(
    () => {
      if (value.lat !== coordinates.lat || value.lon !== coordinates.lon) {
        onChange(coordinates)
      }
    },
    [coordinates.lat, coordinates.lon]
  )
  return (
    <TextField
      label="USNG/MGRS"
      error={!isValid}
      helperText={isValid ? '' : 'invalid USNG value'}
      value={usng}
      onChange={(e: ChangeEvent) => {
        setUSNG(e.target.value)
      }}
      onBlur={() => setUSNG(formattedUSNG)}
      inputProps={{
        style: { textAlign: 'end' },
      }}
      style={{
        width: '12em',
      }}
    />
  )
}

export default PointUSNG
try {
    // @ts-ignore
    pointusng.displayName = "pointusng";
    // @ts-ignore
    pointusng.__docgenInfo = { "description": "", "displayName": "pointusng", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/location/point/point-usng.tsx#pointusng"] = { docgenInfo: pointusng.__docgenInfo, name: "pointusng", path: "src/main/webapp/location/point/point-usng.tsx#pointusng" };
}
catch (__react_docgen_typescript_loader_error) { }