const LocationFilter = ({ onChange }: QueryFilterProps) => (
  <React.Fragment>
    <Box style={filterComponentStyle}>
      <Location
        value={makeDefaultSearchGeo()}
        onChange={value => onChange(geoToFilter(value))}
      />
    </Box>
  </React.Fragment>
)

export default LocationFilter
