import * as React from 'react'
import { coordinates as coordinateEditor } from 'geospatialdraw'
import CoordinateList from './coordinate-list'
import Props from './geo-editor'

const Polygon: React.SFC<Props> = ({ value, onChange, coordinateUnit }) => {
  const {
    id,
    properties,
    coordinates,
    buffer,
    bufferUnit,
  } = coordinateEditor.geoToPolygonProps(value)
  return (
    <CoordinateList
      coordinateList={coordinates}
      coordinateUnit={coordinateUnit}
      buffer={buffer}
      bufferUnit={bufferUnit}
      onChange={(coordinateListValue, bufferValue, bufferUnitValue) =>
        onChange(
          coordinateEditor.polygonPropsToGeo({
            id,
            coordinates: coordinateListValue,
            buffer: bufferValue,
            bufferUnit: bufferUnitValue,
            properties,
          })
        )
      }
    />
  )
}

export default Polygon
try {
    // @ts-ignore
    polygon.displayName = "polygon";
    // @ts-ignore
    polygon.__docgenInfo = { "description": "", "displayName": "polygon", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/main/webapp/location/polygon.tsx#polygon"] = { docgenInfo: polygon.__docgenInfo, name: "polygon", path: "src/main/webapp/location/polygon.tsx#polygon" };
}
catch (__react_docgen_typescript_loader_error) { }